@import "../../global.scss";

.contact{
    // background-color: rgb(221, 55, 55);
    background-color: #474646;
    background-image: linear-gradient(180deg, #474646 0%, #2d2c2c 50%, #1e1e1e 100%);
    display: flex;

    @include mobile {
        flex-direction: column;
    }

    .left{
        flex: 1;

        .img{
            height: 100%;
        }
    }

    .right{
        flex: 1;
        display: flex;
        flex-direction: column;
        align-items: center;
        justify-content: center;

        h2{
            font-size: 30px;
            color: #e2dcdc;
        }

        .container{

        width: 70%;
        height: 80%;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;

        .link{
        width: 10%;
        height: 10%;
        margin: 5px;
        display: flex;
        align-items: center;
        justify-content: center;
        flex-wrap: wrap;
        }
    }

        form{
            width: 70%;
            height: 70%;
            display: flex;
            flex-direction: column;
            align-items: center;
            justify-content: space-around;
        }

        input {
            width: 300px;
            height: 30px;
            font-size: 14px;

            @include mobile {
                width: 200px;
                height: 20px;
            }
        }

        textarea{
            width: 300px;
            height: 200px;
            font-size: 14px;

            @include mobile {
                width: 200px;
                height: 100px;
            }
        }

        // button {
        //     width: 150px;
        //     height: 30px;
        //     color: $mainColor;
        //     background-color: #e2dcdc;
        //     border: none;
        //     border-radius: 10px;
        //     font-weight: 500;
        //     cursor: pointer;

        //     &:focus {
        //         outline: none;
        //     }

        // }

        span{
            color: #e2dcdc;
        }
    }
}