.app {
    height: 100vh;

    .sections{
        width: 100%;
        height: calc(100vh - 70px);
        background-color: rgb(236, 165, 32);
        position: relative;
        top: 70px;
        scroll-behavior: smooth;
        scroll-snap-type: y mandatory;
        scrollbar-width: none; // for Firefox
        &::-webkit-scrollbar{
            display: none; // all other browsers
        }

        > *{
            width: 100vw;
            height: calc(100vh - 70px);
            scroll-snap-align: start;
        }
    }
}
